import { $axios } from "./http";

// 具体商品销售统计
export function getProductStatistics(params) {
  const url = "stastistics_product";
  return $axios.post(url, params);
}

// 商家订单信息汇总
export function getShopStatistics(params) {
  const url = "statistics_shop_order";
  params = {
    ...params,
    pay_method: "1",
  };
  return $axios.post(url, params);
}

// 商家订单信息统计筛选
export function getStatisticsOrderInfo(params) {
  const url = "statistics_order";
  return $axios.post(url, params);
}
